import React from 'react';
import s from './Navbar.module.css'
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';

const Navbar = props => {
  return (
    <nav className={s.Navbar}>
      <NavLink className={s.item} to={props.myID ? '/Profile/' + props.myID : '/Login'}> </NavLink>
      <NavLink className={s.item} to='/Messages'></NavLink>
      <NavLink className={s.item} to='/Friends'></NavLink>
      <NavLink className={s.item} to='/News'></NavLink>
      <NavLink className={s.item} to='/Settings'></NavLink>
    </nav>
  );
}


const mapStateToProps = (state) => {
  return {
    myID: state.login.id,
  }
};

export default connect(mapStateToProps, {})(Navbar);